<template>
    <section>
      <div class="head">
        <i class="iconfont" @click="get">&#xe6bc;</i>
        <span></span>
      </div>
      <div>
        <p></p>
        <input type="text" placeholder="" style="margin-left: 0.15rem;border-bottom: 0.01rem solid lightgrey;" v-model="name">
        <p style="margin-left: 1rem"  @click="sexM">
          <i class="jiantouR" :class="{color: col}">&#xe65c;</i><span></span>
        </p>
        <p  @click="sexW">
          <i class="pin" :class="{color: !col}">&#xe65c;</i><span></span>
        </p>
      </div>
      <div>
        <p></p>
        <input type="text" placeholder="" :class="{kuang: hide}" v-model="phoneNum">
        <i class="jiantouR" style="font-size: 0.25rem;color: #008de1;" @click="hide=true">&#xe60e;</i>
        <input type="text" placeholder="" style="margin-left: 1.05rem" v-if="hide" v-model="phoneNum1">
      </div>
      <div>
        <p></p>
        <input type="text" placeholder="//" style=" border-bottom: 0.01rem solid lightgrey;" @click="tiao" v-model="foodAddress">
        <input type="text" placeholder="()" style="margin-left: 1.05rem" v-model="address">
      </div>
      <div>
        <p></p>
        <input type="text" placeholder="///" style="margin-left: 0.35rem" v-model="tag">
      </div>
        <button class="tijiao" @click="addAddress"></button>
    </section>
</template>

<script>
    export default {
        name: "car_TJ",
      data(){
          return {
            name:'',// 
            phoneNum:'',//
            phoneNum1:'',// 
            foodAddress: this.$store.state.nameOne,// 
            address:'',// 
            tag:'',// 
            radio:1,// 
            sure:false,
            text:'',
            color: '#767676',
            colorRed:'red',
            alertMsg:'',
            popupVisible:false,
            userId:'',// ID
            hide: false,
            col: true
          }
      },
      methods:{
          get(){
            window.history.go(-1)
          },
        block(){
            this.sure=true
        },
        addAddress(){
          this.userId = JSON.parse(localStorage.getItem('success')).id;
            if (this.name == '' || this.phoneNum == '' ||this.phoneNum1 == '' || this.foodAddress == '' || this. address == '' || this.tag == ''){
              this.alertMsg = '';
              this.popupVisible = true;
            } else {
              //  
              const url = `https://elm.cangdu.org/v1/users/${this.userId}/addresses`;
              this.$http({
                method: 'post',
                url: url,
                withCredentials: true, // false
                data:{
                  user_id:this.userId,
                  address: this.foodAddress,
                  address_detail: this.address,
                  geohash: JSON.parse(localStorage.getItem('city')).geohash,
                  name: this.name,
                  phone: this.phoneNum,
                  phone_bk: this.phoneNum1,
                  tag: this.tag,
                  sex: this.radio,
                  tag_type:2
                }
              }).then(res => {
                if (res.data.success){
                 this.$router.push({name: 'car_address'})
                }
              })
            }
            // this.$router.push({name:'car_address'})
          this.name ='';
          this.radio = '';
          this.phoneNum = '';
          this.phoneNum1 = '';
          this.address = '';
          this.tag = '';
          localStorage.removeItem('hsy');
        },
        sexM(){
              this.radio = 1
             this.col = !this.col;
        },
        sexW() {
            this.radio = 2;
            this.col = !this.col;
        },
        tiao() {
            this.$router.push({name: 'car_dz'})
            let obj = {name: this.name,phoneNum: this.phoneNum,phoneNum1: this.phoneNum1,radio: this.radio,address: this.address,tag: this.tag}
            localStorage.setItem('hsy', JSON.stringify(obj));
        }
      },
      mounted() {
          if (JSON.parse(localStorage.getItem('hsy')) != null) {
            let obj1 = JSON.parse(localStorage.getItem('hsy'));
            this.name = obj1.name;
            this.radio = obj1.radio;
            this.phoneNum = obj1.phoneNum;
            this.phoneNum1 = obj1.phoneNum1;
            this.address = obj1.address;
            this.tag = obj1.tag;
          }

      }
    }
</script>

<style scoped>
  section{
    width: 100%;
  }
  .iconfont{
     font-family:"iconfont" !important;
     font-size:0.26rem;font-style:normal;
     -webkit-font-smoothing: antialiased;
     -webkit-text-stroke-width: 0.2px;
     -moz-osx-font-smoothing: grayscale;
     float:left;
     color: white;
   }
  .head{
    width: 100%;
    height: 0.5rem;
    background-color: blue;
    line-height:0.5rem ;
    font-size: 0.2rem;
    text-align: center;
    color: white;
  }
div>p {
  display: inline-block;
  font-size: 0.18rem;
  margin: 0.15rem 0.15rem 0.15rem 0;
}
  div>input {
    width: 64%;
    height: 0.45rem;
  }
  section>div+div {
    border-bottom: 0.01rem solid lightgrey;
    width: 94%;
    margin: 0 auto;
  }
  .tijiao {
    display: block;
    width: 94%;
    height: 0.4rem;
    margin: 0.15rem auto;
    background-color: #4CD964;
    border-radius: 0.07rem;
  }
  .color {
      color: #4CD964;
  }
  .kuang {
    border-bottom: 0.01rem solid lightgrey;
  }
</style>



